$icons: (0:0);
$icons: map-merge($icons,('dev': (X: 0px, Y:-101px, W: 16px, H: 20px, TW: 160px, TH: 147px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('fb': (X: 0px, Y:-131px, W: 7px, H: 16px, TW: 160px, TH: 147px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('logo-color': (X: 0px, Y:0px, W: 160px, H: 41px, TW: 160px, TH: 147px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('logo': (X: 0px, Y:-51px, W: 160px, H: 40px, TW: 160px, TH: 147px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('tw': (X: -26px, Y:-101px, W: 17px, H: 13px, TW: 160px, TH: 147px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('yt': (X: -53px, Y:-101px, W: 16px, H: 11px, TW: 160px, TH: 147px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

