html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* COMPONENTS */
body {
  min-width: 320px;
  font-family: "ProximaNova-Regular", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  color: #000;
}

* {
  -webkit-tap-highlight-color: transparent;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

button {
  background-color: transparent;
}

h1::selection, h2::selection, h3::selection, h4::selection, h5::selection, h6::selection, p::selection, a::selection, span::selection, div::selection, img::selection, mark::selection, ul::selection, li::selection {
  color: #fff;
  background-color: #2ab1b4;
}

img {
  vertical-align: top;
  max-width: 100%;
}

::-ms-clear,
::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input:invalid {
  box-shadow: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

video::-webkit-media-controls-start-playback-button {
  display: none !important;
}

.obj-fit {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.out {
  overflow: hidden;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn {
  display: inline-block;
  padding: 11px 42px;
  box-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
  border-radius: 26px;
  text-transform: uppercase;
  color: #0f2740;
  font-family: "ProximaNova-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 30px;
  transition: background .3s ease-in-out, color .3s ease-in-out;
}

.btn_white {
  background: #fff;
}

.no-touch .btn_white:hover {
  background: #2ab1b4;
  color: #fff;
}

.title-lg {
  font-family: "ProximaNova-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 45px;
  line-height: 50px;
  text-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
  text-transform: uppercase;
  color: #fff;
}

.title-md, .title-sm {
  font-family: "ProximaNova-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 40px;
  color: #0f2740;
  line-height: 50px;
  text-transform: uppercase;
}

.title-sm {
  font-size: 30px;
  line-height: 35px;
}

.header {
  padding: 22px 0;
  background: #fff;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__block {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex: 1 1 100%;
  margin-left: 35px;
  padding-left: 35px;
  border-left: 1px solid #e8e9ed;
}

.header__logo {
  flex: 1 0 160px;
}

.header__contact-info {
  flex: 1 1 100%;
  padding: 10px 0;
}

.header__nav {
  display: inline-flex;
  flex: 1 1 auto;
  padding: 15px 0 0;
}

.header__action-bar {
  display: inline-flex;
  flex: 0 0 65px;
}

.header h1 {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.header_home {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  padding: 52px 0;
  background: transparent;
}

.header_home .contact-info__link-text,
.header_home .nav__list-link {
  color: #fff;
}

.header_home .contact-info__link-icon {
  fill: #b3fcff;
}

.header_home .nav__list-item.is-active {
  border-bottom-color: rgba(133, 200, 207, 0.5);
}

.header_home .action-bar__btn {
  background: #fff;
}

.header_home .action-bar__btn-icon {
  fill: #0f2740;
}

.header_home .header__block {
  border-left-color: rgba(255, 255, 255, 0.2);
}

/* logo
	----------------------------------- */
.logo {
  display: block;
}

.logo_white {
  background-image: url("../img/sprite.png");
  width: 160px;
  height: 40px;
  background-position: 0px -51px;
}

/* nav
	----------------------------------- */
.nav__list {
  display: flex;
  flex: 0 1 764px;
  justify-content: space-between;
}

.nav__list-item {
  padding-bottom: 9px;
  border-bottom: 1px solid transparent;
  line-height: 1;
}

.nav__list-item.is-active {
  border-bottom-color: rgba(255, 255, 255, 0.2);
}

.nav__list-link {
  font-family: "ProximaNova-Semibold", Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  text-transform: uppercase;
  color: #0f2740;
}

/* contact-info
	----------------------------------- */
.contact-info {
  font-size: 0;
}

.contact-info__link {
  display: inline-block;
  vertical-align: top;
  font-size: 0;
}

.contact-info__link:first-child {
  margin-right: 10px;
}

.contact-info__link:not(:first-child) {
  margin-left: 43px;
}

.contact-info__link-icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  fill: #2ab1b4;
  font-size: 20px;
}

.contact-info__link-icon.icon-balloon {
  font-size: 21px;
}

.contact-info__link-icon.icon-mail {
  font-size: 16px;
}

.contact-info__link-text {
  display: inline-block;
  vertical-align: middle;
  font-size: 15px;
  line-height: 1;
  color: #0f2740;
}

/* action-bar
	----------------------------------- */
.action-bar__btn {
  height: 30px;
  width: 30px;
  background: #00899a;
  border-radius: 50%;
  text-align: center;
  line-height: 32px;
  transition: background .3s;
}

.action-bar__btn:not(:first-child) {
  margin-left: 5px;
}

.no-touch .action-bar__btn:hover {
  background: #fff;
}

.no-touch .action-bar__btn:hover .action-bar__btn-icon {
  fill: #00899a;
}

.action-bar__btn-icon {
  fill: #fff;
  transition: fill .3s;
}

.action-bar__btn-icon.icon-search {
  font-size: 12px;
}

.action-bar__btn-icon.icon-mail {
  position: relative;
  font-size: 11px;
}

.screen {
  position: relative;
  background: #2ab1b4;
}

.screen__slider {
  display: none;
  margin-right: -15px;
}

.screen__slider.slick-initialized {
  display: block;
}

.screen__slider-item {
  position: relative;
  cursor: default;
}

.screen__slider-text {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  max-width: 502px;
}

.screen__slider-descr {
  max-width: 390px;
  margin: 35px 0 30px;
  color: #fff;
}

.screen__slider-descr p {
  font-family: "ProximaNova-Light", Arial, sans-serif;
  font-weight: normal;
  font-size: 18px;
}

.screen__slider-descr p:not(:first-child) {
  margin-top: 10px;
}

.screen__slider-btn {
  display: inline-block;
}

.screen__slider-img {
  float: right;
  padding-top: 53.4%;
  width: 70.4%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.screen__slider .slick-dots {
  display: flex;
  position: absolute;
  left: 50%;
  bottom: 50px;
  z-index: 2;
  transform: translateX(-50%);
}

.screen__slider .slick-dots li {
  position: relative;
  width: 14px;
  height: 14px;
  border: 3px solid transparent;
  border-radius: 50%;
  cursor: pointer;
  transition: border-color .7s ease-in-out;
}

.screen__slider .slick-dots li:not(:first-child) {
  margin-left: 3px;
}

.screen__slider .slick-dots li.slick-active {
  border-color: #fff;
}

.screen__slider .slick-dots li.slick-active button {
  background: transparent;
}

.screen__slider .slick-dots button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  width: 10px;
  height: 10px;
  background: #b7e4e7;
  border-radius: 50%;
}

.screen__canvas {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
}

.screen_home {
  padding-top: 200px;
  height: 1140px;
  background-image: linear-gradient(to bottom, #008b9b 17%, #2aa5b1 40%, #5cbfc6 60%, #fff);
}

.services__container {
  display: flex;
}

.services__item {
  position: relative;
  flex: 0 0 20%;
  height: 318px;
  background: #fff;
  border: 1px solid #eeeeee;
  border-right: 0;
}

.services__item:last-child {
  border-right: 1px solid #eeeeee;
}

.services__item:hover .services__item-in {
  height: 480px;
  top: 50%;
  transform: translateY(-50%);
  transition: height 0.3s;
  background-color: #00899a;
}

.services__item:hover .services__item-title,
.services__item:hover .services__item-descr {
  color: #fff;
}

.services__item:hover .services__item-descr {
  visibility: visible;
  opacity: 1;
}

.services__item-in {
  position: absolute;
  right: -1px;
  left: -1px;
  height: 275px;
  z-index: 2;
  padding: 37px 40px;
}

.services__item-title {
  display: block;
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 21px;
  color: #000;
}

.services__item-descr {
  display: block;
  visibility: hidden;
  opacity: 0;
  transition: opacity .8s;
  font-size: 14px;
  line-height: 18px;
}

.services__item-icon {
  font-size: 97px;
}

